$base_fonts: "Courier Prime Sans", monospace;
$emoji_fonts: "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

// Light theme colors
$background_color: #fff;
$text_color: #000;
$link: #000;
$visited_link: #717171;
$hover_link: #393939;
$dimmed_text: #8e8e8e;
$slightly_dimmed_text: #8e8e8e;
$site_footer: $dimmed_text;
$site_footer_hover_link: #bebebe;

// Dark theme colors
$dark_background_color: #000;
$dark_text_color: #fff;
$dark_link: #fff;
$dark_visited_link: #c7c7c7;
$dark_hover_link: #d8d8d8;
$dark_dimmed_text: #8e8e8e;
$dark_slightly_dimmed_text: #8e8e8e;
$dark_site_footer: $dark_dimmed_text;
$dark_site_footer_hover_link: #bebebe;

@import "fonts";

@import "base";
@import "base_dark";
@import "header";
@import "header_dark";
@import "footer";
@import "footer_dark";

body > div {
    max-width: 40rem;
    margin: 1rem auto;
}

article {
    margin: 2rem 0;

    &.post {

        aside {
            margin: 2rem 0;
            font-size: .8rem;
            color: rgb(82,82,82);

            p {
                margin: 0;
            }
        }
    }
}

#posts {
    padding: 0;
    list-style-type: none;
}